    getJSonFile() {
      var selfThis = this
      dbStore.find({ name: this.STOCK.code + this.DEMAData }, function(err, res) {
        console.log(err)
        if (res.length === 1) {
          selfThis.b_index = res.b_index
          selfThis.is_storage = true
        } else {
          selfThis.b_index = '1'
          selfThis.is_storage = false
        }
        selfThis.getHotData()
      })
      dbStore.find({ name: this.STOCK.code + this.CCIData }, function(err, res) {
        console.log(err)
        if (res.length === 1) {
          selfThis.s_index = res.s_index
          selfThis.is_storage_CCI = true
        } else {
          selfThis.b_index = '1'
          selfThis.is_storage_CCI = false
        }
        selfThis.getHotData2()
      })
    },
    getInsertDema() {
      if (this.is_storage) {
        dbStore.update({ name: this.STOCK.code + this.DEMAData }, { b_index: this.b_index }, function(err, res) {
          console.log(err)
          console.log('更新成功')
        })
      } else {
        dbStore.insert({ name: this.STOCK.code + this.DEMAData, b_index: this.b_index }, function(err, res) {
          console.log(err)
          console.log('插入成功')
        })
      }
      this.getHotData()
      this.is_storage = true
    },
    getInsertCCI() {
      if (this.is_storage_CCI) {
        dbStore.update({ name: this.STOCK.code + this.CCIData }, { s_index: this.s_index }, function(err, res) {
          console.log(err)
          console.log('更新成功')
        })
      } else {
        dbStore.insert({ name: this.STOCK.code + this.CCIData, s_index: this.s_index }, function(err, res) {
          console.log(err)
          console.log('插入成功')
        })
      }
      this.getHotData2()
      this.is_storage_CCI = true
    },
    getText(text) {
      var str = text.toString().split('.')
      if (str[0].length > 8) {
        return str[0].substring(0, str[0].length - 8) + '.' + str[0].substring(str[0].length - 8, str[0].length - 6) + '亿'
      } else if (str[0].length > 4) {
        return str[0].substring(0, str[0].length - 4) + '万'
      } else {
        return text
      }
    },
    getZSData() {
      var url = this.baseUrl + '/stock_info/get_zs/'
      this.$http
        .get(url)
        .then(
          response => {
            console.log('连接成功')
            this.zsData = response.body
          },
          response => {
            console.log('连接出错Kline')
          }
        )
        .catch(function(response) {
          console.log('连接catch-Kline' + response)
        })
    },
    getKlineData() {
      var url = this.baseUrl + '/stock_info/get_k/num=' + this.STOCK.code + '/'
      var selfThis = this
      this.$http
        .get(url)
        .then(
          response => {
            selfThis.data2 = response.body.data
            selfThis.getMAData()
            selfThis.getMACDData()
            selfThis.chart1()
            console.log('连接成功')
          },
          response => {
            console.log('连接出错Kline')
          }
        )
        .catch(function(response) {
          console.log('连接catch-Kline' + response)
        })
    },
    getKlineParameterData() {
      var url = this.baseUrl + '/stock_info/get_k_line/num=' + this.STOCK.code + '/'
      var selfThis = this
      this.$http
        .get(url)
        .then(
          response => {
            response.body.inner_market = this.getText(response.body.inner_market)
            response.body.outer_market = this.getText(response.body.outer_market)
            response.body.amount = this.getText(response.body.amount)
            response.body.volume_index = this.getText(response.body.volume_index)
            response.body.main_outflow = this.getText(response.body.main_outflow)
            response.body.main_inflow = this.getText(response.body.main_inflow)

            // selfThis.$refs.inflowsDiv.style.width = response.body.main_inflow_ratio * 100 + '%'
            // selfThis.$refs.outOfDiv.style.width = response.body.main_outflow_ratio * 100 + '%'
            this.klineParameter = response.body
            console.log('连接成功')
          },
          response => {
            console.log('连接出错Klinep')
          }
        )
        .catch(function(response) {
          console.log('连接catch-Kline' + response)
        })
    },
