    getScoreData() {
      var selfThis = this
      this.$http
        .get(this.baseUrl + '/stock_info/get_score/num=' + this.STOCK.code + '/')
        .then(
          response => {
            selfThis.score = response.body.data
            // hot_or_risk 0无 1 预警 2 热点
            if (response.body.data.hot_or_risk === 0) {
              selfThis.imgUrl = require('../assets/image/icon_middle.png')
            } else if (response.body.data.hot_or_risk === 1) {
              selfThis.imgUrl = require('../assets/image/icon_under.png')
            } else if (response.body.data.hot_or_risk === 2) {
              selfThis.imgUrl = require('../assets/image/icon_up.png')
            }
            selfThis.industry = response.body.data.industry
            for (var i = 0; i < response.body.data.remarks.length; i++) {
              if (i === 0) {
                if (selfThis.industry.length) {
                  selfThis.remarks2 = response.body.data.remarks[0]
                }
              } else if (i === 1) {
                if (selfThis.industry.length) {
                  selfThis.remarks2 = response.body.data.remarks[1]
                } else {
                  selfThis.remarks3 = response.body.data.remarks[2]
                }
              } else if (i === 2) {
                if (selfThis.industry.length) {
                  selfThis.remarks3 = response.body.data.remarks[2]
                } else {
                  selfThis.remarks4 = response.body.data.remarks[3]
                }
              } else if (i === 3) {
                if (selfThis.industry.length) {
                  selfThis.remarks4 = response.body.data.remarks[3]
                } else {
                  selfThis.remarks5 = response.body.data.remarks[4]
                }
              } else if (i === 4) {
                if (selfThis.industry.length) {
                  selfThis.remarks5 = response.body.data.remarks[4]
                } else {
                  selfThis.remarks4 = response.body.data.remarks[5]
                }
              } else if (i === 6) {
                selfThis.remarks6 = response.body.data.remarks[5]
              }
            }
            selfThis.$refs.img.style.display = 'block'
          },
          response => {
            console.log('连接出错Score')
          }
        )
        .catch(function(response) {
          console.log('连接Score-catch')
        })
    },
    getHotData() {
      var txt = ''
      var title = this.ToggleCase2(this.DEMAData)
      if (this.b_index === '1') {
        if (title === 't3' || title === 'sar') {
          txt = title + '/num=' + this.STOCK.code + '/'
        } else {
          txt = title + '_5/num=' + this.STOCK.code + '/'
        }
        if (title === 'ht_t') {
          txt = 'httrendline/num=' + this.STOCK.code + '/'
        }
      } else if (this.b_index === '2') {
        txt = title + '_10/num=' + this.STOCK.code + '/'
      } else if (this.b_index === '3') {
        txt = title + '_5_10/num=' + this.STOCK.code + '/'
      }
      this.$http
        .get(this.baseUrl + '/stock_info/get_transaction_data/target=' + txt)
        .then(
          response => {
            this.hotData = response.body.data
            if (this.hotData.bs === '') {
