    getCCIText(index) {
      if (this.CCIData === 'CCI') {
        if (index === 1) {
          return 'CCIX'
        }
      } else if (this.CCIData === 'MACD') {
        if (index === 1) {
          return 'MACD1'
        } else if (index === 2) {
          return 'MACD3'
        }
      } else if (this.CCIData === 'KD') {
        if (index === 1) {
          return 'KDJ1'
        } else if (index === 2) {
          return 'KDJ2'
        } else if (index === 3) {
          return 'KDJ3X'
        } else if (index === 4) {
          return 'KDJ3'
        } else if (index === 5) {
          return 'KDJ4X'
        }
      } else if (this.CCIData === 'MACDEXT') {
        if (index === 1) {
          return 'MACDEXT1'
        } else if (index === 2) {
          return 'MACDEXT3X'
        }
      } else if (this.CCIData === 'DX') {
