          return 'DX1X'
        } else if (index === 2) {
          return 'DX2X'
        }
      } else if (this.CCIData === 'RSI') {
        if (index === 1) {
          return 'RSI1'
        } else if (index === 2) {
          return 'RSI2'
        }
      } else if (this.CCIData === 'MFI') {
        if (index === 1) {
          return 'MFI1X'
        } else if (index === 2) {
          return 'MFI1'
        } else if (index === 3) {
