    chart1(index) {
      var data0 = splitData(JSON.parse(JSON.stringify(this.data2)))
      var title = this.JsonData.title
      var CCITitle = this.CCIData
      var dema = JSON.parse(JSON.stringify(this.DEMA))
      var cci = JSON.parse(JSON.stringify(this.CCI))
      var selfThis = this
      // macd计算
      function splitData(rawData) {
        var categoryData = []
        var values = []
        var volDate = []
        var difs = []
        var deas = []
        var startLength = ''
        var endLength = ''
        var scope = 130

        for (var i = 0; i < rawData.length; i++) {
          categoryData.push(rawData[i].splice(0, 1)[0])
          values.push(rawData[i])
          difs.push(rawData[i][6])
          deas.push(rawData[i][7])
          volDate.push([i, rawData[i][4], rawData[i][0] > rawData[i][1] ? 1 : -1])
        }
        //
        if (rawData.length - scope >= 0) {
          startLength = rawData.length - scope
        } else {
          startLength = 0
        }
        endLength = rawData.length

        return {
          categoryData: categoryData,
          values: values,
          difs: difs,
          deas: deas,
          volDate: volDate,
          startLength: startLength,
          endLength: endLength
        }
      }
      // ma均线函数
      function calculateMA(index) {
        var result = []
        if (title === 'HT_TRENDLINE' || title === 'T3') {
          for (var ii = 0; ii < dema.length; ii++) {
            if (index === 0) {
              result.push(dema[ii])
            } else {
              result.push(null)
            }
          }
        } else {
          for (var i = 0; i < dema.length; i++) {
            result.push(dema[i][index])
          }
        }
        return result
      }

      function getCCI(index) {
        var result = []
        if (CCITitle === 'CCI') {
          selfThis.color_line1 = '#FFFFFF'
          for (var CCIi = 0; CCIi < cci.length; CCIi++) {
            if (index === 1) {
              result.push(cci[CCIi])
            } else {
              result.push(null)
            }
          }
        } else if (CCITitle === 'MACD') {
          selfThis.color_line1 = '#FFFFFF'
          selfThis.color_line2 = '#FFFF0B'
          for (var MACDi = 0; MACDi < cci.length; MACDi++) {
            if (index < 3) {
              if (index === 0) {
                result.push(cci[MACDi][2])
              } else {
                result.push(cci[MACDi][index - 1])
              }
            } else {
              result.push(null)
            }
          }
        } else if (CCITitle === 'KD') {
          selfThis.color_line1 = '#FFFFFF'
          selfThis.color_line2 = '#FFFF0B'
          selfThis.color_line3 = '#FC00FF'
          for (var KDi = 0; KDi < cci.length; KDi++) {
            if (index === 0) {
              result.push(null)
            } else {
              result.push(cci[KDi][index - 1])
            }
          }
        } else if (CCITitle === 'MACDEXT') {
          selfThis.color_line1 = '#FFFFFF'
          selfThis.color_line2 = '#FFFF0B'
          for (var MACDEXTi = 0; MACDEXTi < cci.length; MACDEXTi++) {
            if (index < 3) {
              if (index === 0) {
                result.push(cci[MACDEXTi][2])
              } else {
                result.push(cci[MACDEXTi][index - 1])
              }
            } else {
              result.push(null)
            }
          }
        } else if (CCITitle === 'DX') {
          selfThis.color_line1 = '#FFFFFF'
          selfThis.color_line2 = '#FFFF0B'
          selfThis.color_line3 = '#FC00FF'
          for (var DXi = 0; DXi < cci.length; DXi++) {
            if (index === 0) {
              result.push(null)
            } else {
              result.push(cci[DXi][index - 1])
            }
          }
        } else if (CCITitle === 'RSI') {
          selfThis.color_line1 = '#FFFFFF'
          selfThis.color_line2 = '#FFFF0B'
          for (var RSIi = 0; RSIi < cci.length; RSIi++) {
            if (index === 0) {
              result.push(null)
            } else {
              result.push(cci[RSIi][index - 1])
            }
          }
        } else if (CCITitle === 'MFI') {
          selfThis.color_line1 = '#FFFFFF'
          selfThis.color_line2 = '#FFFF0B'
          selfThis.color_line3 = '#FC00FF'
          for (var MFIi = 0; MFIi < cci.length; MFIi++) {
            if (index === 0) {
              result.push(null)
            } else {
              result.push(cci[MFIi][index - 1])
